body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.app {
  text-align: center;
  padding: 20px;
}

.input-section {
  margin: 20px 0;
}

.weather-card {
  background: lightblue;
  padding: 20px;
  margin: 10px auto;
  width: 90%;
  max-width: 400px;
  border-radius: 10px;
}

.saved-locations ul {
  list-style-type: none;
  padding: 0;
}

.saved-locations li {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}

@media (max-width: 768px) {
  .weather-card {
    width: 100%;
  }
}
